<template>
  <div class="dslfp">
    <div style="display:flex; justify-content:center; margin:10px 0 15px 0;">
      <el-input v-model="keywords"
                placeholder="通过用户名搜索用户..."
                style="width: 400px; margin-right:8px">
      </el-input>
      <el-button type="primary"
                 icon="el-icon-search"
                 @click="doSearch">搜索</el-button>
    </div>
    <div class="admin-container ">
      <el-card class="admin-card"
               v-for="(admin, index) in admins"
               :key="index">
        <div slot="header"
             class="clearfix">
          <span>账号：{{admin.username}}</span>
          <el-button style="float: right; padding: 3px 0; color: #ff0000;"
                     type="text"
                     icon="el-icon-delete"
                     @click="deleteAdmin(admin)"></el-button>
        </div>
        <div>
          <el-popover placement="right"
                      title="角色列表"
                      width="200"
                      @show="showPop(admin)"
                      @hide="hidePop(admin)"
                      trigger="click">
            <el-select v-model="rolesche"
                       multiple
                       placeholder="请选择">
              <el-option v-for="(r, index) in allRoles"
                         :key="index"
                         :label="r.grade_name"
                         :value="r.id">
              </el-option>
            </el-select>
            <el-button slot="reference"
                       type="text"
                       icon="el-icon-more"></el-button>
          </el-popover>
        </div>
      </el-card>
    </div>
    <div style="display: flex; justify-content: flex-end">
      <el-pagination background
                     layout="prev, pager, next, jumper, ->, total"
                     @current-change="currentChange"
                     :page-size="pageSize"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'sys_zabbix_user_up',
  data() {
    return {
      userPage: 1,
      total: 0,
      pageSize: 20,
      allRoles: '',
      rolesche: [],
      filter: '',
      admins: [],
      keywords: '',
    }
  },
  mounted() {
    this.initAdmins()
  },
  methods: {
    currentChange(currenPage) {
      this.userPage = currenPage
      this.initAdmins()
    },
    hidePop(admin) {
      let flag = false
      let rids = ''
      this.rolesche.forEach((sr) => {
        rids += ',' + sr
      })
      let rid = rids.replace(/^,+/, '').replace(/,+$/, '')
      if (admin.grade != rid) {
        flag = true
      }
      if (flag) {
        let url = '/home/User/user_update/uid/' + admin.id
        url = url + '/gradeid/' + rid
        console.log(url)
        this.putRequest(url).then((resp) => {
          if (resp.code == 200) {
            this.initAdmins()
          }
        })
      }
    },
    showPop(admin) {
      this.initAllRoles()
      this.rolesche = []
      let roles = admin.grade.split(',')
      roles.forEach((r) => {
        this.rolesche.push(parseInt(r))
      })
    },
    initAllRoles() {
      this.getRequest('/home/Grade/grade_list/gradeSize/9999999').then(
        (resp) => {
          if (resp.code == 200) {
            this.allRoles = resp.data
          }
        }
      )
    },
    // 删除用户列表
    deleteAdmin(admin) {
      this.$confirm('此操作将永久删除该用户列表, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          return
          this.deleteRequest('/Grade/grade_delete' / +admin.id).then((resp) => {
            if (resp.code == 200) {
              this.initAdmins()
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
          })
        })
    },
    doSearch() {
      // 这里获取用户输入的用户名this.keywords，并且拼接成像后端请求的条件'/userName/' + this.keywords，然后存入data对象下的this.filter
      this.filter = '/userName/' + this.keywords
      // 然后再次根据条件初始化用户列表
      this.initAdmins()
    },
    initAdmins() {
      // 这里组装向后端请求的url（既是请求地址和携带的请求数据）
      let url =
        '/home/User/user_list' + this.filter + '/userPage/' + this.userPage
      this.getRequest(url).then((resp) => {
        if (resp.code == 200) {
          this.admins = resp.data
          this.total = resp.count
        }
      })
    },
  },
}
</script>

<style>
.dslfp {
  background-color: #fff;
}

.admin-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 10px;
}

.admin-card {
  width: 350px;
  margin-bottom: 20px;
}
</style>
